/*
 * =================================================
 * ClearVision Style Override for Chirpy Theme
 * FINAL CONSOLIDATED STYLES
 * =================================================
 */

/* --- 1. Global Background & Variable Overrides --- */

:root[data-mode=dark] {
    --main-bg: transparent !important;
}

body {
    background-image: url('https://raw.githubusercontent.com/Kaytii210/theme/2e5836a07e7f5bc53362e8dbb211eb8ff56b724e/img/theme.webp') !important;
    background-size: cover !important;
    background-position: center !important;
    background-attachment: fixed !important;
    color: #fbfbfb !important;
}

body::before {
    content: '';
    position: fixed;
    top: 0; left: 0;
    width: 100vw;
    height: 100vh;
    background-color: rgba(0, 0, 0, 0.5);
    z-index: -1;
}


/* --- 2. Panel & Card Backgrounds --- */

#sidebar {
    background-color: rgba(0, 0, 0, 0.7) !important;
    backdrop-filter: blur(10px);
    border-color: rgba(255, 255, 255, 0.08) !important;
}

#topbar-wrapper {
    background: transparent !important;
    border: none !important;
    box-shadow: none !important;
}

#topbar {
    background-color: transparent !important;
    backdrop-filter: blur(10px);
    border: none !important;
    box-shadow: none !important;
}

.card-wrapper.card,
.card.categories,
.card.tags,
#archives .card {
    background: transparent !important;
    border: none !important;
    box-shadow: none !important;
    padding: 0 !important;
}

.post-preview {
    background-color: rgba(40, 43, 48, 0.6) !important;
    backdrop-filter: blur(8px);
    border: 1px solid rgba(255, 255, 255, 0.08) !important;
    border-radius: 8px;
    padding: 0;
    display: block;
    transition: all 0.3s ease;
    margin-bottom: 1.5rem;
}

.post-preview:hover {
    transform: translateY(-5px);
    box-shadow: 0 8px 15px rgba(0,0,0,0.2);
}

#panel-wrapper .access {
    background-color: rgba(0, 0, 0, 0.3) !important;
    backdrop-filter: blur(8px);
    border: 1px solid rgba(255, 255, 255, 0.08) !important;
    border-radius: 8px;
    padding: 1rem;
}

.post-tag,
#search-result-wrapper .content {
    background-color: rgba(40, 43, 48, 0.6) !important;
    backdrop-filter: blur(8px);
    border: 1px solid rgba(255, 255, 255, 0.08) !important;
    border-radius: 8px;
}

#sidebar .profile-wrapper {
    background-color: rgba(0, 0, 0, 0.2) !important;
    border: none !important;
    border-radius: 12px;
    width: 100%;
    margin: 2.5rem 0;
    padding: 1.5rem 1.25rem 1.5rem 3.5rem;
}

.card.categories .card-header,
.card.tags .card-header {
    background-color: rgba(40, 43, 48, 0.6) !important;
    backdrop-filter: blur(8px);
    border: 1px solid rgba(255, 255, 255, 0.08) !important;
    border-radius: 8px !important;
    margin-bottom: 1rem;
}

div#archives {
    background-color: rgba(40, 43, 48, 0.6) !important;
    backdrop-filter: blur(8px);
    border: 1px solid rgba(255, 255, 255, 0.08) !important;
    border-radius: 8px !important;
    padding: 1.5rem;
}

#archives .list-unstyled li {
    background: transparent !important;
    border: none !important;
}


/* --- 3. Search Bar --- */

#search-input {
    background-color: transparent !important;
    border-color: transparent !important;
    color: #fbfbfb !important;
}

#search-input::placeholder {
    color: #94949c !important;
}


/* --- 4. Text & Link Colors --- */

.site-title {
    color: #ffffff !important;
}

.site-subtitle,
.post-meta,
.copyright {
    color: #94949c !important;
}

a {
    color: #2780e6 !important;
    transition: color 0.2s ease;
}

a:hover {
    color: #1e63b3 !important;
}

#access-lastmod ul a:hover,
#access-lastmod ul a:focus,
#page-category ul a:hover,
#page-category ul a:focus,
#page-tag ul a:hover,
#page-tag ul a:focus,
#archives ul a:hover,
#archives ul a:focus,
.card.categories .card-header a:hover,
.card.categories .card-header a:focus {
    color: #ffffff !important;
    text-decoration: none !important;
    outline: none !important;
    border: none !important;
    box-shadow: none !important;
}


.post-tag:hover {
    color: #ffffff !important;
    background-color: rgba(50, 53, 58, 0.8) !important;
}


/* --- 5. Sidebar Navigation --- */

#sidebar .nav-link {
    color: #94949c !important;
    margin: 4px 8px;
    padding-left: 12px;
    border-radius: 6px;
    position: relative;
    z-index: 1;
    background-image: linear-gradient(to right, #2780e6 50%, transparent 50%);
    background-size: 200% 100%;
    background-position: right bottom;
    transition: all 0.4s ease-out;
}

#sidebar .nav-link .fas {
    color: #94949c !important;
    transition: color 0.3s ease 0.1s;
}

#sidebar .nav-item.active .nav-link,
#sidebar .nav-item .nav-link:hover {
    background-position: left bottom;
    color: #ffffff !important;
}

#sidebar .nav-item.active .nav-link .fas,
#sidebar .nav-item .nav-link:hover .fas,
#sidebar .nav-item.active .nav-link span,
#sidebar .nav-item .nav-link:hover span {
    color: #ffffff !important;
}
